import React, { useState, useEffect } from "react";
import logo_img from "../../../imageFolder/lala1.png";
import { useNavigate } from "react-router-dom";
import { useRecoilState } from "recoil";

import {
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Spinner,
  useToast,
} from "@chakra-ui/react";
import {
  TokenState,
  UserIdState,
  CoverState,
  fileClickIdState,
  folderClickIdState,
  CompanyListState,
} from "../MainPage/Atom";
import { HamburgerIcon } from "@chakra-ui/icons";
// import axios from "axios";

function NavBar(props) {
  const navigate = useNavigate();
  const [Main, setMain] = useState(false);
  const toast = useToast();

  const [Token, setToken] = useRecoilState(TokenState);
  const [userId, setuserId] = useRecoilState(UserIdState);
  const [Cover, setCover] = useRecoilState(CoverState);
  const [fileClickId, setfileClickId] = useRecoilState(fileClickIdState);
  const [folderClickId, setfolderClickId] = useRecoilState(folderClickIdState);
  const [CompanyList, setCompanyList] = useRecoilState(CompanyListState);

  const handleAccount = () => {
    //      
    navigate("/account");
  };

  const handleProfile = () => {
    //      
    navigate("/profile");
  };

  const navHandler = () => {
    //      
    navigate("/main");
  };

  const logoutHandler = () => {
    //     
    toast({
      position: "bottom-right",
      title: " ",
      description: " .",
      status: "success",
      duration: 2000,
      isCloasabl: true,
    });
    navigate("/");

    setToken("");
    setuserId(0);
    setCover([]);
    setfileClickId(0);
    setfolderClickId(0);
    setCompanyList([]);
  };

  return (
    <div
      className="navbar navbar-light bg-light"
      style={{
        boxShadow: "0px 4px 4px -4px black",
        height: "64px",
        paddingLeft: "20px",
      }}>
      <img //  
        src={logo_img}
        onClick={navHandler}
        style={{ width: "100px", height: "40px", cursor: "pointer" }}
        alt=""
      />
      <div //        
        style={{
          display: "flex",
          alignItems: "center",
        }}>
        {props.Loading ? (
          <Spinner
            thickness="4px"
            speed="0.65s"
            emptyColor="gray.200"
            color="blue.700"
            size="xl"
            marginRight="20px"
          />
        ) : null}
        <button //  
          style={{
            marginRight: "20px",
            width: "min-content",
            // height: "40px",
            fontWeight: "bold",
          }}
          onClick={logoutHandler}>
          logout
        </button>
        <Menu>
          {/*       ,   */}
          <MenuButton>
            <HamburgerIcon
              style={{ width: "40px", height: "40px", marginRight: "9px" }}
            />
          </MenuButton>
          <MenuList minWidth="100px" style={{ width: "120px" }}>
            <MenuItem style={{ width: "120px" }} onClick={handleProfile}>
              
              {/*      */}
            </MenuItem>
            <MenuItem style={{ width: "120px" }} onClick={handleAccount}>
              
              {/*       */}
            </MenuItem>
          </MenuList>
        </Menu>
      </div>
    </div>
  );
}

export default NavBar;
