import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import {
  Grid,
  GridItem,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  FormControl,
  FormLabel,
  Input,
  Button,
  useToast,
  Box,
} from "@chakra-ui/react";
import { RepeatIcon } from "@chakra-ui/icons";
import GrammerForm from "./sections/GrammerForm";
import NavBar from "../NavBar/NavBar";
import Lo from "./sections/Lo";

import "./MainPage.css";

import Form from "./sections/Form";
import GrammerTag from "./sections/GrammerTag";

import Question from "./sections/Question";
import Folder from "./sections/Folder";
//   ,

import HiddenTag from "./sections/HiddenTag";
import MemoForm from "./sections/MemoForm";
import MemoTag from "./sections/MemoTag";

import {
  CoverState,
  CompanyListState,
  MemoState,
  TokenState,
  UserIdState,
  folderClickIdState,
  fileClickIdState,
} from "./Atom";
import { useRecoilState } from "recoil";

function MainPage() {
  // const [Cover, setCover] = useState([]); //  
  const navigate = useNavigate();
  const toast = useToast();
  const [Grammer, setGrammer] = useState(false);
  //       
  const [Memo, setMemo] = useRecoilState(MemoState);
  const [MemoToggle, setMemoToggle] = useState(false);
  const [ListToggle, setListToggle] = useState(true);
  const { isOpen, onOpen, onClose } = useDisclosure();
  const initialRef = React.useRef(null);
  const [Company, setCompany] = useState("");

  const [CompanyList, setCompanyList] = useRecoilState(CompanyListState);
  //  list 
  const [fileClickId, setfileClickId] = useRecoilState(fileClickIdState);
  const [folderClickId, setfolderClickId] = useRecoilState(folderClickIdState);

  const [Loading, setLoading] = useState(false);
  // Loading  
  const [Cov, setCov] = useState("");

  const [Cover, setCover] = useRecoilState(CoverState);
  const [Token, setToken] = useRecoilState(TokenState);
  const [userId, setuserId] = useRecoilState(UserIdState);

  const [GrammerText, setGrammerText] = useState([]);

  const Notoken = async () => {
    setToken("");
    setuserId(0);
    setCover([]);
    setfileClickId(0);
    setfolderClickId(0);
    setCompanyList([]);
  };
  const first = async () => {
    setLoading(true);
    try {
      const response = await axios.get(
        // "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=1"
        `http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=${userId}`,
        {
          headers: {
            Authorization: Token,
            // Authorization: `JWT ${Token}`,
          },
        }
      );
      setCompanyList(response.data.list);

      setCover(response.data.list[0].cover_letter);
      setLoading(false);
      setCov(response.data.list[0].title);

      try {
        const profile = await axios.get(
          `http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/profile?userId=${userId}`,
          {
            headers: {
              Authorization: Token,
            },
          }
        );
        setMemo(profile.data.memo);
        setLoading(false);
      } catch (e) {
        console.log(e);
      }
    } catch (e) {
      setLoading(false);
      toast({
        position: "bottom-right",
        title: "",
        description: "  .",
        status: "error",
        duration: 2000,
        isClosable: true,
      });

      await Notoken();
      navigate("/", { replace: true });
    }
  };

  useEffect(() => {
    //      
    first();
  }, []);

  const toggleListFunction = () => {
    setListToggle(!ListToggle);
  };
  const GrammerReHandler = async () => {
    setLoading(true);
    try {
      const response = await axios.get(
        `http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/spell-check?id=${
          fileClickId === 0 ? Cover[0].id : fileClickId
        }`,
        {
          headers: {
            Authorization: Token,
          },
        }
      );
      setGrammerText(response.data.errInfo);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      await Notoken();
      navigate("/");
    }
  };

  return (
    <Box
      sx={{ "::-webkit-scrollbar": { display: "none" }, overflow: "hidden" }}>
      <Lo isOpen={Loading} />
      <Box
      // style={{ overflow: "hidden" }}
      >
        <NavBar loc="main" Loading={Loading} />
        {/*    */}
      </Box>
      <Grid templateColumns="repeat(20, 1fr)" h="92vh">
        {/* chakra grid ,     20   */}
        <GridItem //  
          //    , 20      .
          colSpan={1}
          style={{
            backgroundColor: "#212226",
            height: "100%",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            overflowY: "scroll",
            overflowX: "hidden",
            minWidth: "100px",
          }}
          sx={{ "::-webkit-scrollbar": { display: "none" } }}>
          <Folder
            Loading={Loading}
            setLoading={setLoading}
            //    
          />
        </GridItem>
        {ListToggle ? (
          <GridItem //  
            sx={{ "::-webkit-scrollbar": { display: "none" } }}
            colSpan={ListToggle ? 3 : 0}
            style={{
              // height: "10px",
              backgroundColor: "#303136",
              height: "100%",
              overflow: "scroll",
            }}>
            <Question
              Cov={Cov}
              setCov={setCov}
              Loading={Loading}
              setLoading={setLoading}
            />
            {/* <HiddenTag
              ListToggle={ListToggle}
              refreshfunction={toggleListFunction}
            /> */}
          </GridItem>
        ) : (
          <GridItem
            colSpan={0}
            style={{
              backgroundColor: "#d9d9d9",
            }}>
            <HiddenTag
              ListToggle={ListToggle}
              refreshfunction={toggleListFunction}
            />
          </GridItem>
        )}
        {/* {Grammer ? ( */}
        {/* //  , */}

        <GridItem //  20  16 
          colSpan={ListToggle ? 16 : 18}
          style={{
            backgroundColor: "#d9d9d9",
            height: "100%",
          }}>
          <div style={{ display: "flex", height: "100%" }}>
            {/* ,         */}
            {ListToggle && (
              <HiddenTag
                ListToggle={ListToggle}
                refreshfunction={toggleListFunction}
              />
            )}
            <Form Loading={Loading} setLoading={setLoading} />
            {/*    */}
            <div
              style={{
                width: "1.5%",
                height: "40%",
                display: "flex",
                flexDirection: "column",
              }}>
              <GrammerTag
                MemoToggle={MemoToggle}
                setMemoToggle={setMemoToggle}
                Grammer={Grammer}
                setGrammer={setGrammer}
              />
              {/*    */}

              <MemoTag
                MemoToggle={MemoToggle}
                setMemoToggle={setMemoToggle}
                Grammer={Grammer}
                setGrammer={setGrammer}
              />
            </div>

            {Grammer ? (
              <div //       
                style={{
                  marginLeft: "0.5%",
                  borderRadius: "10px",
                  backgroundColor: "white",
                  marginTop: "8%",
                  width: "30%",
                  height: "580px",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  overflow: "scroll",
                  marginRight: "1%",
                }}>
                <div //    
                  style={{
                    fontWeight: "900",
                    textAlign: "center",
                    marginTop: "10px",
                    fontSize: "26px",
                    color: "black",
                    alignItems: "center",
                    marginBottom: "5px",
                    display: "flex",
                  }}>
                   
                </div>
                <div>
                  <Button
                    w={4}
                    h={4}
                    style={{ float: "right" }}
                    onClick={GrammerReHandler}>
                    <RepeatIcon />
                  </Button>
                </div>
                <GrammerForm GrammerText={GrammerText} />
                {/*   ( map   ..?!) */}
              </div>
            ) : (
              <div></div>
            )}
            {MemoToggle ? (
              <div //       
                style={{
                  // marginLeft: "0.5%",
                  marginLeft: "0.5%",
                  borderRadius: "10px",
                  backgroundColor: "white",
                  marginTop: "8%",
                  width: "30%",
                  height: "580px",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  marginRight: "1%",
                }}>
                <MemoForm Memo={Memo} setMemo={setMemo} />
              </div>
            ) : (
              <div></div>
            )}
          </div>
        </GridItem>
      </Grid>
    </Box>
  );
}

export default MainPage;
