import React, { Fragment, useEffect, useState } from "react";
import { useRecoilState } from "recoil";
import { AddIcon, DeleteIcon } from "@chakra-ui/icons";
import { useNavigate } from "react-router-dom";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  Button,
  FormControl,
  Input,
  FormLabel,
  useDisclosure,
  Avatar,
  AvatarBadge,
  useToast,
} from "@chakra-ui/react";
import axios from "axios";
import {
  folderClickIdState,
  CoverState,
  CompanyListState,
  TokenState,
  UserIdState,
  fileClickIdState,
} from "../Atom";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { Navigate } from "react-router-dom";

function Folder({ Loading, setLoading }) {
  const navigate = useNavigate();
  const { isOpen, onOpen, onClose } = useDisclosure();
  const initialRef = React.useRef(null);
  const [Company, setCompany] = useState("");
  const [folderClickId, setfolderClickId] = useRecoilState(folderClickIdState);
  const [fileClickId, setfileClickId] = useRecoilState(fileClickIdState);
  const [Cover, setCover] = useRecoilState(CoverState);
  const [CompanyList, setCompanyList] = useRecoilState(CompanyListState);
  // const [Loading, setLoading] = useState(false);
  const [placeholderProps, setPlaceholderProps] = useState({});
  const queryAttr = "data-rbd-drag-handle-draggable-id";
  const [Prev, setPrev] = useState("");
  const [Next, setNext] = useState("");
  const [Token, setToken] = useRecoilState(TokenState);
  const [userId, setuserId] = useRecoilState(UserIdState);
  const toast = useToast();

  const companyHandler = (event) => {
    //       Company 
    setCompany(event.currentTarget.value);
  };

  const companyclickHandler = async () => {
    // save     

    let va = false; //  false (  /   true)

    if (Company && Company.length > 0) {
      //       
      CompanyList.map((list, index) => {
        // map 
        if (list.title === Company) {
          //     
          va = true; //  true 
          return va;
        }
      });
      if (va === false) {
        //  
        //  return  company  state .
        //   save     company   

        const body = {
          user_id: userId,
          title: Company,
        };

        setLoading(true);

        try {
          await axios.post(
            "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/lists",
            body,
            {
              headers: {
                Authorization: Token,
              },
            }
          );
          //  ,
          setLoading(false);
          setCompany(""); //      

          await FolUpdate1(); //   FolUpdate  
        } catch (e) {
          setLoading(false);
          toast({
            position: "bottom-right",
            title: "  ",
            status: "error",
            duration: 2000,
            isClosable: true,
          });
        }
        onClose(); //   
      } else {
        toast({
          //    toast
          position: "bottom-right",
          title: "  ",
          description: "  .",
          status: "error",
          duration: 2000,
          isClosable: true,
        });
      }
    } else {
      toast({
        //     toast
        position: "bottom-right",
        title: "  ",
        description: " .",
        status: "error",
        duration: 2000,
        isClosable: true,
      });
    }
  };

  const deleteHandler = async (id) => {
    if (window.confirm(" ?")) {
      //     
      // setCompanyList(CompanyList.filter((company) => company.list_id !== id));
      //  ,  id  id     CompanyList  
      const body = {
        listId: id,
      };
      setLoading(true);

      try {
        await axios.delete(
          "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/lists",
          {
            params: body,
            headers: {
              Authorization: Token,
            },
          }
        );
        setLoading(false);

        await FolUpdate();
      } catch (e) {
        setLoading(false);

        toast({
          position: "bottom-right",
          title: "  ",
          description: e.response.data,
          status: "error",
          duration: 2000,
          isClosable: true,
        });
      }
    }
  };

  const FolUpdate = async () => {
    //    (   )
    setLoading(true);
    try {
      const response = await axios.get(
        // "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=1"
        `http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=${userId}`,
        {
          headers: {
            Authorization: Token,
            // Authorization: `JWT ${Token}`,
          },
        }
      );
      setCompanyList(response.data.list);
      circleClick(response.data.list[0].list_id);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      toast({
        position: "bottom-right",
        title: "",
        description: "  .",
        status: "error",
        duration: 2000,
        isClosable: true,
      });
      // setToken("");
      setuserId(0);
      setCover([]);
      setfileClickId(0);
      setfolderClickId(0);
      setCompanyList([]);
      // navigate("/");
    }
  };

  const FolUpdate1 = async () => {
    //    (   )
    setLoading(true);
    try {
      const response = await axios.get(
        // "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=1"
        `http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/?userId=${userId}`,
        {
          headers: {
            Authorization: Token,
            // Authorization: `JWT ${Token}`,
          },
        }
      );
      setCompanyList(response.data.list);

      setLoading(false);
    } catch (e) {
      setLoading(false);
      toast({
        position: "bottom-right",
        title: "",
        description: "  .",
        status: "error",
        duration: 2000,
        isClosable: true,
      });
      // setToken("");
      setuserId(0);
      setCover([]);
      setfileClickId(0);
      setfolderClickId(0);
      setCompanyList([]);
      // navigate("/");
    }
  };

  const circleClick = (id) => {
    const cov = CompanyList.filter((company) => company.list_id === id);
    // CompanyList     id   
    setCover(cov[0].cover_letter);
    setfolderClickId(id);
    //    id mainPage 
    setfileClickId(cov[0].cover_letter[0].id);
  };

  const onDragEnd = (result) => {
    // dropped outside the list
    // drag    
    if (!result.destination) {
      // list   destination null 
      // => null   
      return;
    }

    setCompanyList((items) =>
      reorder(items, result.source.index, result.destination.index)
    );

    setPlaceholderProps({});
  };

  const onDragUpdate = (update) => {
    // drag     
    if (!update.destination) {
      // list    null 
      return;
    }
    const draggableId = update.draggableId;
    const destinationIndex = update.destination.index;

    const domQuery = `[${queryAttr}='${draggableId}']`;
    const draggedDOM = document.querySelector(domQuery);
    if (!draggedDOM) {
      return;
    }
    const { clientHeight, clientWidth } = draggedDOM;

    const clientY =
      parseFloat(window.getComputedStyle(draggedDOM.parentNode).paddingTop) +
      [...draggedDOM.parentNode.children]
        .slice(0, destinationIndex)
        .reduce((total, curr) => {
          const style = curr.currentStyle || window.getComputedStyle(curr);
          const marginBottom = parseFloat(style.marginBottom);
          return total + curr.clientHeight + marginBottom;
        }, 0);

    setPlaceholderProps({
      clientHeight,
      clientWidth,
      clientY,
      clientX: parseFloat(
        window.getComputedStyle(draggedDOM.parentNode).paddingLeft
      ),
    });
  };

  const getListStyle = (isDraggingOver) => ({
    width: "100%",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  });

  const getItemStyle = (isDragging, draggableStyle) => ({
    color: "#212226",
    cursor: "pointer",
    fontWeight: "bold",
    marginTop: "40px",

    ...draggableStyle,
  });

  const reorder = (list, startIndex, endIndex) => {
    let result = Array.from(list);

    let prev,
      next = 0;

    if (endIndex === startIndex) {
      return result;
    }

    if (endIndex === 0) {
      //       ,
      prev = null;
      next = result[endIndex].list_id;
    } else if (endIndex === result.length - 1) {
      //      
      prev = result[endIndex].list_id;
      next = null;
    } else if (endIndex - startIndex > 0) {
      prev = result[endIndex].list_id; //    
      next = result[endIndex + 1].list_id;
    } else {
      prev = result[endIndex - 1].list_id; //    
      next = result[endIndex].list_id;
    }

    if (Math.abs(endIndex - startIndex) === 1 && endIndex - startIndex > 0) {
      //     ,     
      if (prev !== null) {
        prev = result[endIndex].list_id;
      }
      if (next !== null) {
        next = result[endIndex + 1].list_id;
      }
    } else if (
      Math.abs(endIndex - startIndex) === 1 && //     ,     
      endIndex - startIndex < 0
    ) {
      if (prev !== null) {
        prev = result[endIndex - 1].list_id;
      }
      if (next !== null) {
        next = result[endIndex].list_id;
      }
    }

    const body = {
      list_id: result[startIndex].list_id,

      to_move_prev_list_id: prev,
      to_move_next_list_id: next,
    };

    if (result.length !== 1) {
      server(body);
    }

    // const [removed] = result.splice(startIndex, 1);
    // result.splice(endIndex, 0, removed);

    return result;
  };

  const server = async (body) => {
    setLoading(true);

    try {
      await axios.put(
        "http://ec2-13-209-139-191.ap-northeast-2.compute.amazonaws.com/lists/position",
        body,
        {
          headers: {
            Authorization: Token,
          },
        }
      );
      setLoading(false);

      await FolUpdate();
    } catch (e) {
      setLoading(false);
      console.log(e);
    }
  };

  const onSubmitClick = (e) => {
    if (e.key === "Enter") {
      companyclickHandler();
    }
  };

  return (
    <Fragment>
      <DragDropContext onDragEnd={onDragEnd} onDragUpdate={onDragUpdate}>
        <Droppable droppableId="droppable">
          {(provided, snapshot) => (
            <div
              {...provided.droppableProps}
              ref={provided.innerRef}
              style={
                // width: "100%",
                // height: "100%",
                getListStyle(snapshot.isDraggingOver)
              }>
              {CompanyList &&
                CompanyList.map((company, index) => (
                  <Draggable
                    key={`${company.list_id}`}
                    draggableId={`${company.list_id}`}
                    //      
                    index={index}>
                    {/* index  , draggableId   */}
                    {(provided, snapshot) => (
                      <Avatar //  
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        {...provided.dragHandleProps}
                        type="button"
                        className="rounded-circle"
                        onClick={() => circleClick(company.list_id)}
                        name={company.title}
                        getInitials={() => `${company.title}`}
                        key={index}
                        size="50"
                        width="55px"
                        height="55px"
                        _hover={{ width: "60px", height: "60px" }}
                        bg="white"
                        style={getItemStyle(
                          snapshot.isDragging,
                          provided.draggableProps.style
                        )}>
                        <AvatarBadge
                          borderColor="white"
                          bg="white"
                          boxSize="1.25em">
                          <DeleteIcon
                            style={{ width: "90%", height: "90%" }}
                            onClick={() => deleteHandler(company.list_id)}
                          />
                        </AvatarBadge>
                      </Avatar>
                    )}
                  </Draggable>
                ))}
              {provided.placeholder}
            </div>
          )}
        </Droppable>
      </DragDropContext>

      <button //  
        className="rounded-circle"
        onClick={onOpen}
        style={{
          minWidth: "55px",
          minHeight: "55px",
          backgroundColor: "white",
          marginTop: "40px",
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}>
        <AddIcon />
      </button>
      <Modal initialFocusRef={initialRef} isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader style={{ marginTop: "10px" }}>
             
          </ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <FormControl>
              <FormLabel> </FormLabel>
              <Input
                ref={initialRef}
                placeholder="Company"
                value={Company}
                onChange={companyHandler}
                focusBorderColor="gray.300"
                onKeyPress={onSubmitClick}
              />
            </FormControl>
          </ModalBody>

          <ModalFooter>
            <Button
              colorScheme="blue"
              mr={3}
              onClick={() => {
                companyclickHandler();
                // onClose();
              }}>
              Save
            </Button>
            <Button
              onClick={() => {
                onClose();
                setCompany("");
              }}>
              Cancel
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </Fragment>
  );
}

export default Folder;
